  display: flex;
}

.container {
  text-align: center;
  background-color: #fff;
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, .1);
}

.loader {
  width: 48px;
  height: 48px;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  display: block;
  position: relative;
}

.loader:after, .loader:before {
  content: "";
  box-sizing: border-box;
  width: 48px;
  height: 48px;
  background: #d9e6d9;
  border-radius: 50%;
  animation: animloader 2s linear infinite;
  position: absolute;
  top: 0;
  left: 0;
}

.loader:after {
  animation-delay: 1s;
}

@keyframes animloader {
  0% {
    opacity: 1;
    transform: scale(0);
  }

  100% {
    opacity: 0;
    transform: scale(1);
  }
}

